import { useEffect, useState } from 'react';
import { connect, useDispatch } from 'react-redux';
import { resetSelectedProStep, setSelectedProStep, setShowMenu, setSelectedProcess, setShowSideMenu } from '../../../../reduxStore/actions/flowActions';
import { APP_NAME } from '../../../../shared/utility';
import cls from './ProcessInfo.module.scss';
import { store } from '../../../../reduxStore/store';
import { useNavigate } from 'react-router-dom';
import UserCard from '../../../common/UserCard/UserCard';
import HeaderTopBar from '../../../common/HeaderTopBar/HeaderTopBar';
import { Button } from '../../../common';

const Workflows = props => {

	const { onSetSelectedProcess, companyProcesses, companyData } = props;
	document.title = `Workflows | ${APP_NAME}`;
	const dispatch = useDispatch();
	const navigate = useNavigate();

	useEffect(() => {

		dispatch(setShowSideMenu(false));
		dispatch(setShowMenu(true));
	}, []);
	return (
		<div className={cls.usersHost}>
			<div style={{ margin: '100px 0 0 0' }}>
				<HeaderTopBar headerTitle={'Workflows'} targetProcessing={false} />
			</div>

			<Button
				titleStyle={{ fontWeight: 'normal' }}
				title={'New Workflow'} clicked={() => {
					store.dispatch(setShowMenu(false));
					onSetSelectedProcess({ description: '', id: '', is_pinned: false, name: '', user_id: '', registered: false, type: false });
					navigate('/workflow');
				}} style={{ width: '160px', display: (companyData?.status === 'active') ? '' : (companyData?.status === 'on_trial' && companyProcesses.length < 10) ? '' : (companyData?.status === 'free' && companyProcesses.length === 0) ? '' : 'none' }} />
			<br />
			<div className={cls.colleaguesSection}>
				<div className={cls.rightSideContainer}>
					{companyProcesses?.length > 0 &&
						companyProcesses?.map((item, index) => {
							return (
								<div key={index} style={{ padding: '10px' }}>
									<UserCard
										noPhoto={true}
										height={'24vh'}
										firstName={item?.name}
										email={item?.description?.slice(0, 100)}
										cardClicked={() => {
											store.dispatch(setShowMenu(false));
											store.dispatch(setShowSideMenu(true));
											onSetSelectedProcess({
												name: item?.name,
												description: item?.description,
												id: item?.id,
												is_pinned: item?.is_pinned,
												user_id: item?.user_id,
												registered: true,
												type: item?.type,
												steps: item?.steps,
												shared_positions: item?.shared_positions
											});
											navigate('/workflow');
										}}
									/>
								</div>
							);
						})
					}
				</div>
			</div>
		</div>
	);
}
const mapStateToProps = store => {
	const { selectedProcess, blueprints, allModules, companyProcesses, workflowTemplates, companyData } = store.flowReducer;
	return { selectedProcess, blueprints, allModules, companyProcesses, workflowTemplates, companyData };
};
const mapDispatchToProps = dispatch => {
	return {
		onSetSelectedProStep: selectedStep => dispatch(setSelectedProStep(selectedStep)),
		onResetSelectedProStep: () => dispatch(resetSelectedProStep()),
		onSetSelectedProcess: process => dispatch(setSelectedProcess(process))
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Workflows);
